@include lt {
  &-body {
    &__login,
    &__register,
    &__pincode {
      font-size: 16px;
      color: #404040;
      font-family: Montserrat, sans-serif;

      background: #f5f5f5;
      margin: 0;
      padding: 2rem 0;
      display: grid;
      @media (max-width: 576px) {
        display: inline;
      }
      place-items: center;
      box-sizing: border-box;
    }
  }
}

@include lotto(".lt") {
  &-login,
  &-register {
    background-color: #fff;
    width: 360px;
    max-width: 360px;
    background: rgb(14, 109, 158);
    background: linear-gradient(180deg, rgba(14, 109, 158, 1) 0%, rgba(59, 172, 214, 1) 100%);
    @media (max-width: 576px) {
      width: 100%;
      max-width: 100%;
      height: 100%;
    }

    display: flex;
    align-items: center;
    justify-content: center;

    @include lotto(" .lt") {
      &-login,
      &-register {
        &__panel {
          margin: 5.7rem 0;
        }
        &__head {
          font-family: Poppins-Bold;
          font-size: 30px;
          color: #eeab77;
          line-height: 1.2;
        }
        
      }
    }
  }
}

@include lt {
  &-wrap {
    overflow: hidden;

    form {
      width: 100%;
      margin: 3rem 0 0 0;
    }

    @include lt {
      &-wrap {
        &__form {
          @include lotto("&.lt-validate") {
            &__form {
              position: relative;
            }
          }
          &--title {
            display: block;
            font-family: Poppins-Bold;
            font-size: 30px;
            color: #eeab77;
            line-height: 1.2;
            text-align: center;
            margin-top: 2rem;
            margin-bottom: 4rem;
            i {
              font-size: 60px;
            }
          }
        }
        &__input {
          width: 100%;
          position: relative;
          border-bottom: 2px solid #adadad;
          margin-bottom: 37px;

          @include lt {
            &-input {
              font-family: Poppins-Regular;
              font-size: 15px;
              color: #fff;
              line-height: 1.2;
              display: block;
              width: 100%;
              height: 45px;
              background: transparent;
              padding: 0 5px;
              &:focus {
                @include lotto("+.lt-focus") {
                  &::after {
                    top: -15px;
                  }
                  &::before {
                    width: 100%;
                  }
                }
              }
              &.has-val {
                @include lotto("&+.lt-focus") {
                  &::after {
                    top: -15px;
                  }
                  &::before {
                    width: 100%;
                  }
                }
              }
            }
          }

          @include lt {
            &-focus {
              position: absolute;
              display: block;
              width: 100%;
              height: 100%;
              top: 0;
              left: 0;
              pointer-events: none;
              &:before {
                content: "";
                display: block;
                position: absolute;
                bottom: -2px;
                left: 0;
                width: 0;
                height: 2px;
                -webkit-transition: all 0.4s;
                -o-transition: all 0.4s;
                -moz-transition: all 0.4s;
                transition: all 0.4s;
              }
              &:after {
                font-family: Poppins-Regular;
                font-size: 15px;
                color: #fff;
                line-height: 1.2;
                content: attr(data-placeholder);
                display: block;
                width: 100%;
                position: absolute;
                top: 16px;
                left: 0px;
                padding-left: 5px;
                -webkit-transition: all 0.4s;
                -o-transition: all 0.4s;
                -moz-transition: all 0.4s;
                transition: all 0.4s;
              }
            }
          }

          @include lotto("&.lt-validate") {
            &__input {
              position: relative;
            }
          }
        }
        &__container {
          display: -webkit-box;
          display: -webkit-flex;
          display: -moz-box;
          display: -ms-flexbox;
          display: flex;
          flex-wrap: wrap;
          justify-content: center;
          padding-top: 13px;
          @include lt {
            &-wrap {
              &__form {
                width: 100%;
                display: block;
                position: relative;
                z-index: 1;
                border-radius: 25px;
                overflow: hidden;
                margin: 0 auto;
                &--bgbtn {
                  position: absolute;
                  z-index: -1;
                  width: 300%;
                  height: 100%;
                  background: #eeab77;
                  top: 0;
                  left: -100%;
                  -webkit-transition: all 0.4s;
                  -o-transition: all 0.4s;
                  -moz-transition: all 0.4s;
                  transition: all 0.4s;
                }
                &--btn {
                  font-family: Poppins-Medium;
                  font-size: 15px;
                  color: #fff;
                  line-height: 1.2;
                  display: -webkit-box;
                  display: -webkit-flex;
                  display: -moz-box;
                  display: -ms-flexbox;
                  display: flex;
                  justify-content: center;
                  align-items: center;
                  padding: 0 20px;
                  width: 100%;
                  height: 50px;
                  &:hover .lt-wrap__form--bgbtn {
                    left: 0;
                  }
                }
                &--bgregister {
                  position: absolute;
                  z-index: -1;
                  width: 300%;
                  height: 100%;

                  top: 0;
                  left: -100%;
                  -webkit-transition: all 0.4s;
                  -o-transition: all 0.4s;
                  -moz-transition: all 0.4s;
                  transition: all 0.4s;
                }
                &--register {
                  font-family: Poppins-Medium;
                  font-size: 15px;
                  color: #fff;
                  line-height: 1.2;
                  display: -webkit-box;
                  display: -webkit-flex;
                  display: -moz-box;
                  display: -ms-flexbox;
                  display: flex;
                  justify-content: center;
                  align-items: center;
                  padding: 0 20px;
                  width: 100%;
                  height: 50px;
                  &:hover .lt-wrap__form--bgregister {
                    left: 0;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}
