




.body__profile {
    font-size: 16px;
    color: #404040;
    font-family: Montserrat, sans-serif;
    // background-image: linear-gradient(to bottom right, #ff9eaa 0% 65%, #e860ff 95% 100%);
    // background-position: center;
    // background-attachment: fixed;
    background:#f5f5f5;
    margin: 0;
    padding: 2rem 0;
    display: grid;
    @media (max-width: 576px) {
        display: inline;
    }
    place-items: center;
    box-sizing: border-box;
}

@include lotto(".card.lt") {
    &-profile {
        background-color: #fff;
        width: 360px;
        max-width: 360px;
        display: flex;
        flex-direction: column;
        overflow: hidden;
        border-radius: 1rem;

        @media (max-width: 576px) {
            width: 100%;
            max-width: 100%;
            border-radius: 0;
        }
      
        @include lotto(" .lt") {
            &-profile {
                &__head {
                    width: 100%;
                    position: absolute;
                    overflow: hidden;
                    padding: 4rem 2rem 3rem;
                    display: -ms-flexbox !important;
                    display: flex !important;
                    color: #fff !important;
                   
                    &.between {
                        -ms-flex-pack: justify !important;
                        justify-content: space-between !important;
                    }
                    &.center {
                        -ms-flex-pack: center !important;
                        justify-content: center !important;
                    }

                    &--left {
                        display: flex;
                        img {
                            border-radius: 50%;
                            width: 4rem;
                            height: 4rem;
                            margin-right: 0.5rem;
                        }
                        h6 {
                            margin-bottom: 0px;
                            font-weight: 700;
                        }
                        small {
                            margin-bottom: 0px;
                        }
                        div {
                            padding-top: 0.7rem;
                        }
                    }
                    &--right {
                        padding: 0.5rem;
                        -ms-flex-direction: column !important;
                        flex-direction: column !important;
                        display: -ms-flexbox !important;
                        display: flex !important;
                        color: #fff !important;
                        text-align: center;

                        h4 {
                            margin-bottom: 0px;
                            font-weight: 700;
                        }
                        small {
                            margin-bottom: 0px;
                        }
                    }
                }
                &__banner {
                    background: rgb(14, 109, 158);
                    background: linear-gradient(180deg, rgba(14, 109, 158, 1) 0%, rgba(59, 172, 214, 1) 100%);
                    // background-image: url("https://images.unsplash.com/photo-1545703549-7bdb1d01b734?ixlib=rb-1.2.1&q=80&fm=jpg&crop=entropy&cs=tinysrgb&w=400&fit=max&ixid=eyJhcHBfaWQiOjE0NTg5fQ");
                    // background-position: center;
                    // background-repeat: no-repeat;
                    // background-size: cover;
                    height: 11rem;
                    display: flex;
                    align-items: flex-end;
                    justify-content: center;
                    box-sizing: border-box;

                    svg {
                        background-color: #fff;
                        width: 8rem;
                        height: 8rem;
                        box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.3);
                        border-radius: 50%;
                        transform: translateY(50%);
                        transition: transform 200ms cubic-bezier(0.18, 0.89, 0.32, 1.28);
                    }
                    button ,label{
                        color: #1374a4;
                        background-color: #fff;
                        padding: 0.7rem 1.9rem;
                        box-shadow: 0 0.1rem 0.5rem rgba(0, 0, 0, 0.3);
                        border-radius: 24px;
                        transform: translateY(50%);
                        transition: transform 200ms cubic-bezier(0.18, 0.89, 0.32, 1.28);
                        font-size: 1rem;
                        font-weight: bold;
                    }
                }
                &__body {
                    padding: 3rem 1rem;
                    background: #f5f5f5;
                    height: 350px;
                }
            }
        }
    }

    &-list {
        border: none;
        border-radius: 5px;
        margin-bottom: 5px;
        @include lotto(" .lt") {
            &-list {
                &__body {
                    -ms-flex: 1 1 auto;
                    flex: 1 1 auto;
                    padding: 0rem;
                    display: -ms-flexbox !important;
                    display: flex !important;

                    &--board {
                        width: 100px;
                        padding: 0.5rem;
                        background: #0e6d9e;
                        background: linear-gradient(180deg, #1397db, #5bd3ff);
                        border-top-left-radius: 5px;
                        border-bottom-left-radius: 5px;
                        display: grid;
                        justify-content: center;
                        align-items: center;
                        span {
                            color: #fff;
                            font-size: 0.8rem;
                            font-weight: bold;
                        }
                        p {
                            font-size: 0.5rem;
                            color: #fff;
                            margin-bottom: 0px;
                        }
                    }
                    &--detail {
                        padding: 0.5rem;
                        width: 100%;
                        text-align: center;

                        @include lt {
                            &-title {
                                padding-bottom: 0px;
                                color: #a0a0a0;
                                font-size: 0.85rem;
                                text-align: center;
                            }
                        }
                        hr {
                            margin: 8px 2rem;
                        }
                        .follow {
                            &-info {
                                display: flex;
                                h2 {
                                    text-align: center;
                                    width: 50%;
                                    margin: 0;
                                    box-sizing: border-box;
                                    display: flex;
                                    flex-direction: column;
                                    border-radius: 0.8rem;

                                    span {
                                        color: #1c9eff;
                                        font-size: 13px;
                                        margin-bottom: 5px;
                                    }
                                    small {
                                        color: #afafaf;
                                        font-size: 0.5rem;
                                    }
                                    // &:hover {
                                    //     background-color: #f2f2f2;
                                    //     span {
                                    //         color: #007ad6;
                                    //     }
                                    // }
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}
