@include lotto(".lt") {
    &-pincode {
        background-color: #fff;
        width: 360px;
        max-width: 360px;
        background: rgb(14, 109, 158);
        background: linear-gradient(180deg, rgba(14, 109, 158, 1) 0%, rgba(59, 172, 214, 1) 100%);
        @media (max-width: 576px) {
            width: 100%;
            max-width: 100%;
            height: 100%;
        }

        display: flex;
        align-items: center;
        justify-content: center;

        @include lotto(" .lt") {
            &-pincode {
                &__head {
                    font-family: Poppins-Bold;
                    font-size: 30px;
                    color: #eeab77;
                    line-height: 1.2;
                    margin: auto 2.8rem;
                }
                &__body {
                    min-height: 360px;
                    display: -webkit-box;
                    display: -webkit-flex;
                    display: -moz-box;
                    display: -ms-flexbox;
                    display: flex;
                    flex-wrap: wrap;
                    justify-content: center;

                    @include lotto(" .lt-pin") {
                        &__title {
                            text-align: center;
                            margin-bottom: 2rem;
                            h2 {
                                display: block;
                                font-family: Poppins-Bold;
                                color: #eeab77;
                                line-height: 1.2;
                            }
                            small {
                                color: #fff;
                            }
                        }
                        &__code {
                            display: flex !important;
                            -ms-flex-pack: center !important;
                            justify-content: center !important;
                            margin:2rem 0 1rem 0.9rem;
                            .items {
                                background: #69a9cf;
                                border-radius: 6px;
                                margin-right: 1rem!important;
                                width: 10px;
                                height: 10px;
                                align-content: center;
                                justify-content: center;
                                display: grid;
                                font-size: 1.2rem;
                                font-weight: bold;
                                color: #1c627c;
                                &.check {
                                    background: #417897;
                                    border:#69a9cf 1px solid
                                }
                            }
                        }
                        &__number {
                            margin:1rem 0 1rem 0.9rem;
                            .group-items {
                                display: flex !important;
                                -ms-flex-pack: center !important;
                                justify-content: center !important;
                                .items {
                                    margin-right: 1rem!important;
                                    color: #69a9cf;
                                    text-align: center;
                                    border-radius: 50%;
                                    font-size: 1.2rem;
                                    font-weight: 700;
                                    box-shadow: none !important;
                                    i {
                                        font-size: 1rem;
                                    }
                                    span {
                                        font-size: 0.6rem;
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

